
import TrackingMap from "@/components/map/tracking-map";
import TrackingTopBar from "@/components/tracking/top-bar";
import DriverCard from "@/components/tracking/driver-card";
import Image from "next/image";
import { Alert, AlertTitle, AlertDescription } from "@/components/ui/alert";
import { Terminal } from "lucide-react";

export default function TrackingPage() {
  const apiKey = process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY;

  return (
    <div className="flex h-screen w-screen">
      <div className="flex-1 relative">
        <TrackingTopBar />
        {!apiKey || apiKey === "YOUR_API_KEY_HERE" ? (
            <div className="w-full h-full bg-muted flex items-center justify-center relative">
                <Image src="https://picsum.photos/seed/trackingmap/1600/900" alt="Static map placeholder" layout="fill" objectFit="cover" className="opacity-50" data-ai-hint="map background" />
                <Alert variant="default" className="max-w-md z-10 bg-background/80 backdrop-blur-sm">
                    <Terminal className="h-4 w-4" />
                    <AlertTitle>Live Map is Disabled</AlertTitle>
                    <AlertDescription>
                        A Google Maps API key is required to display the live tracking map. A static placeholder is being shown. To enable the live map, add your key to the `.env` file.
                    </AlertDescription>
                </Alert>
            </div>
        ) : (
            <TrackingMap apiKey={apiKey} />
        )}
      </div>

      <aside className="w-[360px] border-l bg-background hidden md:block">
        <DriverCard />
      </aside>
    </div>
  );
}
